@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;700;900&display=swap);
* {
  outline: none;
}

body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

.btn {
  border-radius: 50px;
  padding: 14px 0;
  width: 160px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}

.btn:active {
  transform: translateY(2px);
}

.primary-btn {
  color: var(--white);
  border: 2px solid white;
  font-size: 12px;
}
/* @media only screen and (max-width: 414px) {
  .primary-btn {
    font-size: 13px;
  }
} */

.primary-btn:hover {
  color: aliceblue;
}

.highlighted-btn {
  color: var(--white);
  font-size: 12px;
  border: 2px solid white;
  /* testing purposes */
  color: aqua;
  border: aqua solid 3px;
}

.highlighted-btn:hover {
  color: white;
}
/* @media only screen and (max-width: 568px) {
  .highlighted-btn {
    z-index: -900;
  }
} */

.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  transform: translateY(80px);
  transition: all 0.8s ease;
}

:root {
  /* colors */
  --light-blue: #04c3ff;
  --light-blue-shadow: rgb(69 211 255 / 27%);
  --sea-green: #0abead;
  --sea-green-dark: #0cbfae;
  --sea-green-extra-dark: #09bead;
  --sea-green-shadow: rgba(11, 190, 173, 0.4);
  --header-hamburger-background: rgba(11, 190, 173, 0.8);
  --orange: #f57f17;
  --dark-orange: #ff5823;
  --orange-shadow: rgba(245, 127, 23, 0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message: #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow: #9c9c9ce3;
  --toast-success: #0cbfae;
  --loader-color: #0cbfae;

  /* Font properties */
  --ff-primary-100: "Poppins Thin";
  --ff-primary-200: "Poppins ExtraLight";
  --ff-primary-300: "Poppins Light";
  --ff-primary-400: "Poppins Regular";
  --ff-primary-500: "Poppins Medium";
  --ff-primary-600: "Poppins SemiBold";
  --ff-primary-700: "Poppins Bold";
  --ff-primary-800: "Poppins ExtraBold";
  --ff-primary-900: "Poppins Black";
  /* font sizes */
}

.navbar-container {
  display: flex;
  justify-content: center;
  height: 3.5rem;
  color: var(--white);
  background: rgba(150, 58, 231, 0.9);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  /* backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px); */
}

.navbar-parent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 90%;
  color: var(--white);
}

.navbar-logo {
  font-size: 1.5rem;
  font-family: "Poppins ExtraBold";
  text-transform: uppercase;
  color: var(--white);
}

.navbar-options {
  display: flex;
  align-items: center;
}

.navbar-option {
  font-size: 1rem;
  cursor: pointer;
  font-weight: 600;
}

.navbar-hamburger {
  display: none;
  color: var(--white);
}

.navbar-option-separator {
  margin: 0 1.8rem 0 0;
  color: var(--white);
}
.selected-navbar-option {
  color: #0d0d25;
}

@media only screen and (max-width: 44rem) {
  .navbar-hamburger {
    display: block;
  }

  .navbar-parent {
    width: 100%;
    padding: 0 2rem;
    position: relative;
  }

  .navbar-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: -100%;
    top: 0px;
    height: 100vh;
    width: 100%;
    justify-content: space-around;
    background-color: rgba(150, 58, 231, 0.96);
    z-index: 300;
    opacity: 0.8;
    transition: all 0.5s ease-out;
  }

  .navbar-option-separator {
    margin: 0;
  }

  /* .selected-navbar-option {
      color: var(--white);
  } */

  .navbar-hamburger-bars {
    font-size: 2rem;
    cursor: pointer;
    margin: 0 0.9rem;
  }

  .show-hamburger-options {
    left: 0;
    opacity: 1;
  }
}

.profile-container {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/btaylor13/react-personal-site/client/static/media/dark-code.5b7788d7.jpg);
  color: white;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  padding: 9rem 0;
}

.header-icon {
  font-size: 1.3rem;
  padding: 1rem;

  border: 3px solid #a152e9;
  border-radius: 50%;
  margin: 1rem;
  color: white;
  cursor: pointer;
}

.header-icon:hover {
  box-shadow: 0 0 3px #a097ef, 0 0 7px #c097ef, 0 0 7px rgb(247, 214, 255) inset;
  transform: translateY(-2px);
  transition: all 0.3s ease;
}
.profile-buttons {
  display: flex;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.profile__details-name {
  font-weight: bold;
  font-size: 3.3rem;
}

.profile-role {
  font-weight: 300;
}
.profile-role-tagline {
  font-weight: 400;
}

.primary-btn {
  border: none;
  background-color: #7fb56e;
  box-shadow: 0 0 3px #a6ef97;
  font-size: 1rem;
}

.primary-btn:hover {
  box-shadow: 0 0 7px #c2ef97, 0 0 13px #c2ef97,
    0 0 13px rgb(226, 255, 214) inset;
  transition: all 0.3s ease;
}

.profile-buttons .highlighted-btn {
  font-size: 1rem;
}

@media only screen and (max-width: 29rem) {
  .profile-buttons {
    flex-direction: column;
    margin-top: 0.5rem;
  }

  .profile-buttons .highlighted-btn {
    margin-top: 1rem;
  }
}


.heading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 0 3.1rem 0;
}

.screen-heading {
  font-size: 2rem;
  color: #0d0d25;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.screen-subheading {
  letter-spacing: 1px;
  margin: 0.3rem 0.5rem 0.3rem 0.5rem;
  font-size: 0.8rem;
  color: #0d0d25;
}

.heading-separator {
  display: flex;
  align-items: center;
  position: relative;
  width: 11.25rem;
  margin: 10px 0 0 0;
}

.separator-line {
  width: 100%;
  height: 2px;
  background-color: #0d0d25;
}
.separator-blob {
  height: 10px;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  top: -2.9px;
}

.separator-blob div {
  width: 2.2rem;
  border-radius: 0.8rem;
  background-color: rgba(150, 58, 231, 1);
}

.about-me-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 3rem 0 0 0;
  background-color: white;
}

.about-me-parent {
  max-width: 1000px;
  width: 85%;
}

.about-me-card {
  display: flex;
  width: 100%;
  box-shadow: 0 0px 20px -2px #1f2235;
}

.about-me-profile {
  width: 50%;
  background-image: url(/btaylor13/react-personal-site/client/static/media/hobbies-img1.4d0e9009.JPG);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.about-me-details {
  width: 48%;
  text-align: justify;
  padding: 2rem;
}

.about-me-description {
  font-size: 0.75rem;
}

.about-me-skills {
  margin: 2rem 0;
}

.skills-heading {
  font-weight: 600;
  font-size: 0.9rem;
  margin: 0 0 0.4rem 0;
}

.skills {
  display: flex;
  align-items: center;
  margin: 0.2rem 0;
  font-size: 0.75rem;
}

.skill-blob {
  height: 0.8rem;
  width: 0.8rem;
  margin: 0 10px 0 0;
  background-color: rgb(150, 58, 231);
  border-radius: 50%;
}

.about-me-options .highlighted-btn {
  margin: 0 0 0 1rem;
}

/* Media Queries */

@media only screen and (max-width: 61rem) {
  .about-me-options {
    display: flex;
    flex-direction: column;
  }
  .about-me-options .highlighted-btn {
    margin: 1rem 0 0 0;
  }
}

@media only screen and (max-width: 44rem) {
  .about-me-parent {
    width: 93%;
  }
}

@media only screen and (max-width: 39rem) {
  .about-me-profile {
    display: none;
  }
  .about-me-details {
    width: 100%;
  }
}

@media only screen and (max-width: 29rem) {
  .about-me-options .btn {
    width: 100%;
  }
}

.resume-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  margin: 120px 0;
  /* testing */
  /* background-color: lightsalmon; */
}

.resume-card {
  display: flex;
  height: 513px;
  width: 1000px;
  /* testing */
  /* background-color: goldenrod; */
}

.resume-bullets {
  box-shadow: 15px 0 9px -15px #1f2235;
  height: 100%;
  width: 320px;
}
.resume-bullet-details {
  flex-grow: 1;
  width: 600px;
  overflow: hidden;
  padding: 0 0 0 60px;
}

.experience-description {
  margin: 11px 0 0 0;
  text-align: justify;
  display: flex;
  flex-direction: column;

  /* max-width: 100%; */
}

.resume-description-text {
  font-size: 12px;
}

.resume-details-carousel {
  transition: transform 1s ease-out;
}

.bullet-container {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;
}

.bullets {
  width: 80%;
  position: relative;
  z-index: 2;
}

.bullet {
  display: flex;
  align-items: center;
  background-color: #1f2235;
  height: 40px;
  margin: 15px 0;
  padding: 0 8px;
  border-radius: 20px;
  width: 30px;
  transition: width 0.6s ease;
  cursor: pointer;
}

.bullet-label {
  font-size: 14px;
  white-space: nowrap;
  font-family: "Poppins SemiBold";
}

.bullet-logo {
  height: 16px;
  margin: 0 30px 0 0;
}

.bullet-icons {
  width: 34px;
  height: 100%;
  z-index: 1;
  background-color: #1f2235;
  position: absolute;
}

.selected-bullet {
  color: white;
  width: 100%;
}

.resume-screen-container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 513px;
  width: 100%;
}

.resume-heading {
  display: flex;
  flex-direction: column;
}

.resume-main-heading {
  position: relative;
  display: flex;
  justify-content: space-between;
  font-weight: 600;
}

.heading-bullet {
  position: absolute;
  left: -23px;
  height: 13px;
  width: 13px;
  top: 4px;
  background-color: #9639e7;
  border-radius: 50%;
}

.resume-subheading {
  font-size: 14px;

  font-weight: 500;
  color: #111;
  margin: 3px 0 0 0;
}

.resume-heading-description {
  font-size: 12px;
  text-align: justify;
  margin-top: 3px;
}

.heading-date {
  background-color: #9639e7;
  padding: 4px 12px;
  font-size: 14px;
  border-radius: 14px;
  color: white;
}

.skill-parent {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 50%;
}

.programming-skills-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 0.8rem;
}

.about-me-options .highlighted-btn {
  color: #9639e7;
  border: 3px solid #9639e7;
  font-size: 1rem;
}

.about-me-options .highlighted-btn:hover {
  box-shadow: 0 0 7px #a097ef, 0 0 13px #c097ef,
    0 0 13px rgb(247, 214, 255) inset;
  transition: all 0.3s ease;
}

.code-link {
  font-size: 0.8rem;
  color: #9639e7;
  text-decoration: underline;
}

/* Media Queries */

@media only screen and (max-width: 1110px) {
  .resume-card {
    width: 880px;
  }

  .resume-bullet-details {
    padding: 0 0 0 40px;
  }
}

@media only screen and (max-width: 1023px) {
  .resume-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .resume-screen-container {
    justify-content: flex-start;
    grid-row-gap: 19px;
    row-gap: 19px;
  }

  .bullets {
    width: 90%;
  }

  .resume-card {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    margin: 30px 0;
    align-items: center;
    width: 90%;
  }

  .resume-bullet-details {
    height: 513px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0;
  }

  .resume-bullets {
    width: 100%;
    margin: 30px 0;
  }

  .selected-bullet {
    width: 100%;
  }
}

@media only screen and (max-width: 600px) {
  .heading-date {
    display: none;
  }
  .resume-screen-container {
    justify-content: flex-start;
    grid-row-gap: 13px;
    row-gap: 13px;
  }
}

