.testimonial-section {
  padding: 5rem 0 5rem;
  background-color: #070714;
  position: relative;
  z-index: -1;
}

.testimonial-section .testimonial-item {
  background-color: white;
  padding: 1.875rem;
  border-radius: 3.125rem 3.125rem 3.125rem 0;
}

.testimonial-section .testimonial-item p {
  font-size: 1rem;
  line-height: 1.625rem;
  font-weight: 400;
  margin: 0 0 0.625rem;
  font-style: italic;
}

.testimonial-section .testimonial-item p .fa-quote-left {
  margin-right: 5px;
  color: #9639e7;
}
.testimonial-section .testimonial-item p .fa-quote-right {
  margin-left: 5px;
  color: #9639e7;
}

.owl-nav button {
  position: absolute;
  background-color: #000;
  color: #fff;
  margin: 0;
  transition: all 0.3s ease-in-out;
}

.owl-nav button.owl-prev {
  left: 0;
}
.owl-nav button.owl-next {
  right: 0;
}

.owl-nav button.owl-next,
.owl-nav button.owl-prev {
  padding: 3rem;
  height: 3rem;
  width: 3rem;
}

.owl-nav button.owl-next span,
.owl-nav button.owl-prev span {
  color: #9639e7;
  font-size: 2rem;
  font-weight: 500;
}

.owl-dots {
  text-align: center;
  padding-top: 0.938rem;
}
.owl-dots button.owl-dot {
  width: 0.938rem;
  height: 0.938rem;
  border-radius: 50%;
  display: inline-block;
  background: #ccc;
  margin: 0 3px;
}

.owl-dots button.owl-dot.active {
  background-color: #9639e7;
}

.owl-dots button.owl-dot:focus {
  outline: none;
}

.owl-nav button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.38) !important;
  background-color: #9639e7;
}

.scroll-container {
  position: absolute;
  text-align: center;
  right: 0;
  margin-right: 9px;
  overflow: hidden;
  flex-direction: column;
  margin-top: -45px;
  z-index: 100;
  cursor: pointer;
}
@media only screen and (min-width: 475px) {
  /* .scroll-container {
    margin-bottom: 10px;
  } */
}
.btn-scroll {
  width: 50px;
  height: 50px;
  color: aliceblue;
  font-size: 24px;
  background-color: #070714;
  border-radius: 10px;
  border: none;
}

.btn-scroll i {
  animation: mover 0.5s infinite alternate;
}
@keyframes mover {
  100% {
    transform: translateY(-5px);
  }
  0% {
    transform: translateX(0px);
  }
}

.main-container {
  padding-top: 6rem;
  border-radius: 10px;
}
.contact-form {
  background-color: #9639e7;
  color: white;
  max-width: 700px;
  padding: 2rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 1rem;

  margin-bottom: 2rem;
}

.contact-form h2 {
  letter-spacing: 0.3rem;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.form-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5rem;
}

.form-description {
  max-width: 450px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.contact-form form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  width: 90%;
  margin: 0 auto;

  justify-content: center;
}

.f-icon {
  font-size: 1.3rem;
  padding: 1rem;
  border: 3px solid #070714;
  border-radius: 50%;
  margin: 1rem;
  color: #070714;
  cursor: pointer;
}

.f-icon:hover {
  border: 3px solid rgba(7, 7, 20, 0.6);
  color: rgba(14, 14, 34, 0.6);
  transition: all 0.5s;
}

form p {
  text-align: center;
  margin-bottom: 0.5rem;
  color: #070714;
  letter-spacing: 0.2rem;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
}

.email-link {
  text-decoration: underline;
  color: #070714;
}

.contact-description {
  padding-top: 1rem;
}

form input,
form textarea {
  margin-bottom: 1rem;
  width: 100%;
  border: solid 2px #070714;
  border-radius: 7px;
  padding: 0.3rem;
}

.send-btn {
  display: flex;
  justify-content: center;
}

.send-btn button {
  background-color: #070714;
  color: white;
  padding: 1em 1.3em;
  border: none;
  border-radius: 7px;
}

.send-btn button:hover {
  background-color: rgba(7, 7, 20, 0.7);
  transition: all 0.5s;
}

textarea:focus,
input:focus {
  border: 2px solid rgba(30, 85, 350, 0.47);
}

/* MEDIA QUERIES */

@media only screen and (max-width: 34rem) {
  .contact-form {
    border-radius: 0;
    margin-bottom: 0;
  }
}

